}

.checkbox-row-wise {
    /* background-color: aqua; */
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    overflow-x: auto;
    overflow-y: hidden;
    /* white-space: nowrap;  */
}

.comp-format-header-drop-down-view {
    padding-left: 45px;
    width: 50%;
}

.transfer-image-view {
    /* background-color: aqua; */
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
}

.comp-ladder-recalculate {
    font-size: 15px;
    color: #1b1b34;
    font-weight: 500;
    font-family: inter-medium;
    margin-left: 15px;
}

.time-slot-table .ant-table-tbody>tr>td {
    border-bottom: 0px;
    padding: 6px 6px;
    overflow-wrap: break-word;
}

.time-slot-table .ant-table-thead>tr>th {
    border-bottom: 0px;
    font-weight: 500;
    font-family: inter-medium;
    font-size: 13px;
    padding: 7px 7px;
    overflow-wrap: break-word;
}

.time-slot-table .ant-table-thead>tr.ant-table-row-hover:not(.ant-table-expanded-row):not(.ant-table-row-selected)>td, .time-slot-table .ant-table-tbody>tr.ant-table-row-hover:not(.ant-table-expanded-row):not(.ant-table-row-selected)>td, .time-slot-table .ant-table-thead>tr:hover:not(.ant-table-expanded-row):not(.ant-table-row-selected)>td, .time-slot-table .ant-table-tbody>tr:hover:not(.ant-table-expanded-row):not(.ant-table-row-selected)>td {
    background: transparent;
}

.comp-buttons-view {
    /* background-color: aqua; */
    text-align: right;
}

.comp-finals-button-view {
    display: flex;
    justify-content: flex-end;
}

@media(max-width:991px) {
    .comp-buttons-view .open-reg-button {
        margin-top: 20px;
    }
    .comp-buttons-view .save-draft-text {
        margin-right: 20px;
    }
}

@media(max-width:767px) {
    .comp-buttons-view {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin-top: 10px;
    }
    .reg-add-save-button {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin-top: 10px;
    }
    .comp-buttons-view .ant-btn {
        margin-top: 12px;
    }
    .ant-checkbox-wrapper+.ant-checkbox-wrapper {
        margin-left: 0;
    }
    .ant-select-selection__rendered {
        max-width: 136px;
        overflow: hidden;
    }
    .comp-buttons-view .save-draft-text {
        margin-right: 0px;
    }
    .comp-finals-button-view {
        display: flex;
        justify-content: center;
    }
    .breadcrumb-product {
        font-size: 18px;
    }
    .breadcrumb-add {
        font-size: 18px;
    }
}

